.navbar {
    display: flex;
    align-items: center;
    top: 0px;
    left: 0px;
    right: 0px;
    /* max-width: 600px; */
    margin: 0;
    border: 3px solid var(--titleColor);
    padding: 5px; 
    border-radius: 18px; 
    text-align: center; 
    background-color: #3b4c6a;
    transition: background-size 0.3s; 
    position: fixed;
    box-shadow: inset 0 0 50px rgba(0, 0, 0, 0.7);
}
.navbar h1 {
    margin-left: 10px;
    color: var(--titleColor);
}
.navbar .links {
    margin-left: auto;
    margin-right: 20px;
}
.navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
    border: 1.5px solid var(--titleColor);
    border-radius: 10px;
    box-shadow: inset 0 0 10px rgba(0, 0, 0, 0.7);
}
.navbar a:hover {
    color: var(--hoverColor);
}
 
/* Styles for smaller screens (e.g., phones) */
@media only screen and (max-width: 600px) {
    .navbar {
        flex-direction: column; /* Change the flex direction for smaller screens */
    }
    .navbar h1 {
        font-size: 1.2em;
    }
    .navbar .links {
        margin: 10px;   /* Add margin to separate links on smaller screens */
        order: 1;       /* Move links to the top on smaller screens */
    }

    .navbar a {
        margin: 10; /* Remove margin between links on smaller screens */
    }
} 